<template>
    <div>
        <!-- -->
        <div class="header">
            <div class="form">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="">
                        <el-input v-model="formInline.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit"></el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="group">
                <!--
                    
                -->
                <el-button size="small" type="warning" icon="el-icon-plus"></el-button>
                <el-button size="small" type="danger" icon="el-icon-delete"></el-button>
            </div>
        </div>

        <!--:data="tableData  data"
                el-table 
                el-table-column  prop=date prop
                label=""  label
                width
            -->
        <div class="content">
            <el-table :data="tableData" style="width: 100% " stripe border header-row-class-name="active-header">
                <el-table-column type="selection" width="40"></el-table-column>
                <el-table-column prop="id" label="" width="180" align="center">
                </el-table-column>
                <el-table-column prop="element_class" label="" width="180" align="center">
                </el-table-column>
                <el-table-column prop="element_name" label="" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column label="" align="center">
                    <!--template scope.$index  scope.row -->
                    <template slot-scope="scope">
                        <!---->
                        <el-button type="primary" size="mini" icon="el-icon-edit"
                            @click="handleEdit(scope.$index, scope.row)"></el-button>

                        <el-button slot="reference" size="mini" type="danger" icon="el-icon-delete"
                            @click="handleDelete(scope.$index, scope.row)"></el-button>

                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <Pagination :total="total" @CurrentChange="CurrentChange"></Pagination>
            </div>
        </div>
    </div>
</template>

<script>
//
import Pagination from '@/component/pagination/page-tabs.vue'
export default {
    components: {
        Pagination
    },
    data() {
        return {
            //formInline
            formInline: {
                name: ''
            },
            //tableDatatotal10
            tableData: [],
            total: 10,
        }
    },
    methods: {
        async onSubmit() {
            //api searchformInline.namesearch
            console.log('submit!', this.formInline);
            let search = this.formInline.name;
            let res = await this.$api.search({ search });
            this.tableData = res.data.data;
            this.total = res.data.length;
        },
        handleEdit(index, row) {
            console.log(index, row);
        },
        //delete api 
        handleDelete(index, row) {
            console.log(index, row);
            //this.$confirmelement-ui
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                //deleteElements
                this.deleteElements(row.id);

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });


        },
        CurrentChange(val) {
            console.log('', val);
            this.elementList(val);
        },
        async elementList(page) {
            let res = await this.$api.elementList({ page })
            console.log('', res, page);
            this.tableData = res.data.data;
            this.total = res.data.length;
        },
        async deleteElements(id) {
            //api
            let res = await this.$api.deleteElements({ id });
            console.log('--------', res.data);
            if (res.data.status == 200) {
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }
            //
            this.elementList(1);
        }
    },
    created() {
        this.elementList(1);
    }
}
</script>

<style lang="less" scoped>
.header {
    background: #fff;
    margin-bottom: 20px;
    padding: 10px;

    .el-from-item {
        margin-bottom: 16px;
    }

    .group {
        border: 1px solid #eee;
        padding: 8px;

    }
}

.content {
    background: #fff;

    /deep/ .active-header {
        color: #333;
    }

    .paginationP {
        padding: 20px;
    }
}
</style>