<template>
    <div>
        <div class="header">
            <h3>
                {{ total }}
            </h3>
        </div>

        <div class="wrapper">
            <el-container>
                <el-header>
                    <el-row style="text-align: right">
                        <el-button type="primary" @click="CreateNewPL" class="detailed">
                            
                        </el-button>
                        <el-select v-model="value" placeholder="" style="padding : 20px">
                            <el-option v-for="item in options" :key="item.value" :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-row>
                </el-header>
                <el-main>
                    <template>
                        <el-table stripe=true :data="Data" height=590 border style="width: 100%">
                            <el-table-column prop="name" label="" width="350" align="center"
                                show-overflow-tooltip="true">
                            </el-table-column>
                            <el-table-column prop="description" label="" width="600" align="center"
                                show-overflow-tooltip="true">
                            </el-table-column>
                            <el-table-column prop="impactScore" label="" width="100" align="center"
                                show-overflow-tooltip="true">
                                <template slot-scope="scope">
                                    {{ scope.row.tcp_ports_cnt + scope.row.udp_ports_cnt }}
                                </template>
                            </el-table-column>
                            <el-table-column prop="tcp_ports_cnt" label="TCP" width="100" align="center"
                                show-overflow-tooltip="true">
                            </el-table-column>
                            <el-table-column prop="udp_ports_cnt" label="UDP" width="100" align="center">
                            </el-table-column>
                            <el-table-column label="" align="center">
                                <!--template scope.$index  scope.row -->
                                <template slot-scope="scope">
                                    <!---->
                                    <el-button type="primary" size="mini" icon="el-icon-edit"
                                        @click="StrategyDetail(scope.$index, scope.row)"></el-button>
                                    <el-button type="primary" size="mini" icon="el-icon-edit"
                                        @click="StrategyEdit(scope.$index, scope.row)"></el-button>
                                    <el-button type="danger" size="mini" icon="el-icon-delete"
                                        @click="StrategyDelete(scope.$index, scope.row)">
                                    </el-button>

                                </template>
                            </el-table-column>
                        </el-table>
                    </template>

                </el-main>
            </el-container>
            <div class="pagination">
                <Pagination :total="total" @CurrentChange="CurrentChange"></Pagination>
            </div>
        </div>
    </div>
</template>

<script>
import Pagination from '@/component/pagination/page-tabs.vue'
export default {
    components: {
        Pagination
    },
    data() {
        return {
            total: 0,
            Data: [],
            page: 1,
        }
    },
    methods: {
        CreateNewPL() {
            this.$router.push('/strategy/addstrategy')
        },
        CurrentChange(val) {
            console.log('', val);
            this.strategyList(val);
            this.page = val;
        },
        async strategyList(page) {
            //let column = 1;
            let res = await this.$api.GetPorts();
            this.Data = res.data.data.slice((page - 1) * 10, page * 10);
            console.log('', this.Data, page, res);
            this.total = res.data.data.length;
        },
        StrategyDetail(index, row) {
            console.log(index, row);
            //index += (this.page - 1) * 10;
            console.log(row)
            this.showdetail(row)
        },
        showdetail(val) {
            this.$router.push(
                {
                    path: '/strategy/strategydetail',
                    query: { index: val }
                })
        },
        StrategyEdit(index, row) {
            console.log(index, row);
            this.$router.push(
                {
                    path: '/strategy/strategyedit',
                    query: { index: row }
                })
        },
        StrategyDelete(index, row) {
            console.log(index, row);
            console.log(row.name);
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                //deleteElements
                this.deleteStrategy(row.name);

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                });
            });
        },
        async deleteStrategy(name) {
            let res = await this.$api.DeletePortList({ name });
            console.log('--------', res.data);
            if (res.data.status == 200) {
                this.$message({
                    type: 'success',
                    message: '!'
                });
            }
            //
            this.strategyList(1);
        }
    },
    created() {
        this.strategyList(1);
        console.log('what???');
    }
}
</script>


<style lang="less" scoped>
.header {
    height: 55px;
    background: #fff;
    border-bottom: 2px solid black;
    display: flex;
    align-items: center;

}

.wrapper {
    padding: 10px;
}
</style>