<template>
  <div>
    <div class="header">
      <h3>
        ({{ total }})
      </h3>
    </div>

    <div class="wrapper">
      <el-container>
        <el-header>
          <el-row style="text-align: right">
            <el-button type="primary" @click="CreateNewTask" class="detailed">
              
            </el-button>
            <el-select v-model="value" placeholder="" style="padding : 20px">
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-row>
        </el-header>
        <el-main>
          <template>
            <el-table stripe=true :data="Data" height=590 border style="width: 100%" :key="itemKey">
              <el-table-column prop="name" label="" width="150" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="description" label="" width="200" align="center"
                show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="target" label="" width="120" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="asmethod" label="" width="110" align="center"
                show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="type" label="" width="130" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="schedule_time" label="" width="120" align="center"
                show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="create_time" label="" width="120" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="last_modifid_time" label="" width="120" align="center"
                show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="status" label="" width="120" align="center" show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <p v-if="scope.row.status==4"></p>
                  <p v-else-if="scope.row.status==1"></p>
                  <p v-else-if="scope.row.status==3"></p>
                  <p v-else-if="scope.row.status==2"></p>
                </template>
              </el-table-column>
              <el-table-column prop="progress" label="" width="100" align="center"
                show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-progress :text-inside="true" :stroke-width="26"
                    :percentage="scope.row.status == 1 ? 0 : scope.row.status == 2 ? 25 :scope.row.status == 3 ? 25+75*scope.row.progress/100 : scope.row.status == 4 ? 100 : 0"></el-progress>
                </template>
              </el-table-column>
              <el-table-column label="" align="center">
                <!--template scope.$index  scope.row -->
                <template slot-scope="scope">
                  <!---->
                  <el-button type="primary" size="mini" icon="el-icon-circle-check"
                    @click="TaskStart(scope.$index, scope.row)"></el-button>
                  <el-button type="primary" size="mini" icon="el-icon-edit"
                    @click="TaskDetail(scope.$index, scope.row)"></el-button>

                  <el-button type="primary" size="mini" icon="el-icon-edit"
                    @click="TaskChange(scope.$index, scope.row)"></el-button>

                  <el-button slot="reference" size="mini" type="danger" icon="el-icon-delete"
                    @click="TaskDelete(scope.$index, scope.row)"></el-button>

                </template>
              </el-table-column>
            </el-table>
          </template>

        </el-main>
      </el-container>

      <div class="pagination">
        <Pagination :total="total" @CurrentChange="CurrentChange"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from '@/component/pagination/page-tabs.vue'
export default {
  components: {
    Pagination
  },
  props: {
    tableData: {
      type: Array,
      default: () => [],
    }
  },
  data() {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }],
      current_page:1,
      itemKey : 0,
      total: 20,
      Data: this.tableData,
    }
  },
  methods: {
    CreateNewTask() {
      this.$router.push('/task/addtask')
    },
    CurrentChange(val) {
      console.log('', val);
      this.taskList(val);
      this.current_page = val;
    },
    async taskList(page) {
      let res = await this.$api.GetTask();
      this.Data = res.data.data.slice((page - 1) * 10, page * 10);
      console.log('', this.Data, page, res);
      this.total = res.data.data.length;
    },
    async taskList1() {
      let res = await this.$api.GetTask();
      this.Data = res.data.data.slice(0, 10);
    },
    TaskDetail(index, row) {
      console.log(index, row);
      this.showdetail(row)
    },
    showdetail(val) {
      this.$router.push(
        {
          path: '/task/taskdetail',
          query: { index: val }
        })
    },
    TaskChange(index, row) {
      console.log(index, row);
      this.showchange(row)
    },
    showchange(val) {
      this.$router.push(
        {
          path: '/task/taskchange',
          query: { index: val }
        })
    },
    TaskDelete(index, row){
      console.log(index, row);
      console.log(row.name);
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //deleteElements
        this.delete_task(row.name);

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    async delete_task(name){
      let res = await this.$api.DeleteTask({name});
      console.log('--------', res.data);
      if (res.data.status == 200) {
        this.$message({
          type: 'success',
          message: '!'
        });
      }
      //
      this.taskList(1);
    },
    TaskStart(index, row){
      console.log(index, row);
      this.start_task(row.name)
      //while(this.Data[index].status!=4){
      //  setInterval(this.taskList(this.current_page),1000);
      //}
    },
    async start_task(name){
      let res = await this.$api.StartTask({name});
      console.log(res);
    }
    
  },
  created() {
    this.taskList(1);
    this.total = this.tableData.length;
  },
    mounted() {
      this.timer = setInterval(this.taskList1, 3000);
    },
    beforeDestroy() {
      clearInterval(this.timer);
    }
}
</script>

<style lang="less" scoped>
.header {
  height: 55px;
  background: #fff;
  border-bottom: 2px solid black;
  display: flex;
  align-items: center;

}

.wrapper {
  padding: 10px;
  width: auto;
}
</style>