<template>
  <div>
    <div class="header">
      <h3>
        {{ total }}
      </h3>
    </div>

    <div class="wrapper">
      <el-container>
        <el-header>
          <el-row style="text-align: right">
            <el-button type="primary" @click="CreateNewTarget" class="detailed">
              
            </el-button>
            <el-select v-model="value" placeholder="" style="padding : 20px">
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-row>
        </el-header>
        <el-main>
          <template>
            <el-table stripe=true :data="Data" height=590 border style="width: 100%">
              <el-table-column prop="name" label="" width="150" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="description" label="" width="270" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="hosts" label="" width="250" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="port_list" label="" width="160" align="center" show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="create_time" label="" width="250" align="center">
              </el-table-column>
              <el-table-column prop="last_modify_time" label="" width="250" align="center">
              </el-table-column>
              <el-table-column label="" align="center">
                <!--template scope.$index  scope.row -->
                <template slot-scope="scope">
                  <!---->
                  <el-button type="primary" size="mini" icon="el-icon-edit"
                    @click="TargetDetail(scope.$index, scope.row)"></el-button>

                  <el-button type="primary" size="mini" icon="el-icon-edit"
                    @click="TargetEdit(scope.$index, scope.row)"></el-button>

                  <el-button slot="reference" size="mini" type="danger" icon="el-icon-delete"
                    @click="TargetDelete(scope.$index, scope.row)"></el-button>

                </template>
              </el-table-column>
            </el-table>
          </template>

        </el-main>
      </el-container>
      <div class="pagination">
        <Pagination :total="total" @CurrentChange="CurrentChange"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Pagination from '@/component/pagination/page-tabs.vue'
export default {
  components: {
    Pagination
  },
  props: {
    tableData: {
      type: Array,
      default: () => [],
    }
  },
  data() {
    return {
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }],
      total: 20,
      Data: [],
      page: 1,
    }
  },
  methods: {
    CreateNewTarget() {
      this.$router.push('/target/addtarget')
    },
    CurrentChange(val) {
      console.log('', val);
      this.targetList(val);
      this.page = val;
    },
    async targetList(page) {
      let res = await this.$api.GetTarget();
      this.Data = res.data.data.slice((page - 1) * 10, page * 10);
      console.log('', this.Data, page, res);
      this.total = res.data.data.length;
    },
    TargetDetail(index, row) {
      console.log(index, row);
      index += (this.page - 1) * 10;
      this.showdetail(index)
    },
    TargetDelete(index, row) {
      console.log(index, row);
      console.log(row.name);
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //deleteElements
        this.deleteTarget(row.name);

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });

    },
    TargetEdit(index, row) {
      console.log(index, row);
      index += (this.page - 1) * 10;
      this.showedit(index)
    },
    showedit(val) {
      this.$router.push(
        {
          path: '/target/targetedit',
          query: { index: val }
        })
    },
    async deleteTarget(name) {
      let res = await this.$api.DeleteTarget({ name });
      console.log('--------', res.data);
      if (res.data.status == 200) {
        this.$message({
          type: 'success',
          message: '!'
        });
      }
      //
      this.targetList(1);
    },
    showdetail(val) {
      this.$router.push(
        {
          path: '/target/targetdetail',
          query: { index: val }
        })
    }
  },
  created() {
    this.targetList(1);
    console.log('what???');
    this.total = this.tableData.length;
  }
}
</script>

<style lang="less" scoped>
.header {
  height: 55px;
  background: #fff;
  border-bottom: 2px solid black;
  display: flex;
  align-items: center;

}

.wrapper {
  padding: 10px;
}
</style>